import React from 'react';
import { Link } from 'react-router-dom';
import { useAppContext } from '../context/AppContext';
import { isCanadianProduct } from '../services/api';

const ProductCard = ({ product, isCanadian = null }) => {
  const { darkMode } = useAppContext();
  
  // If isCanadian is not provided, determine it from the product
  const productIsCanadian = isCanadian !== null ? isCanadian : isCanadianProduct(product);
  
  // Extract product details
  const productName = product.name || product.product_name || 'Unknown Product';
  const brand = product.brand || product.brands || '';
  const imageUrl = product.image_url || '';
  const barcode = product.barcode || product.code || '';
  
  return (
    <Link 
      to={`/product/${barcode}`}
      className={`block rounded-lg overflow-hidden shadow-md transition duration-200 hover:shadow-lg ${
        darkMode ? 'bg-dark-surface hover:bg-dark-surface-hover' : 'bg-white hover:bg-gray-50'
      }`}
    >
      <div className="relative">
        {/* Product image */}
        <div className={`h-48 ${darkMode ? 'bg-gray-800' : 'bg-gray-100'} flex items-center justify-center`}>
          {imageUrl ? (
            <img 
              src={imageUrl} 
              alt={productName} 
              className="h-full w-full object-contain"
            />
          ) : (
            <div className="text-gray-400">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-12 w-12" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
              </svg>
            </div>
          )}
        </div>
        
        {/* Canadian badge */}
        {productIsCanadian && (
          <div className="absolute top-2 right-2 bg-maple-red text-white text-xs font-bold px-2 py-1 rounded-full flex items-center">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3 mr-1" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
            </svg>
            Canadian
          </div>
        )}
      </div>
      
      <div className="p-4">
        {/* Product name */}
        <h3 className={`font-semibold mb-1 line-clamp-2 ${darkMode ? 'text-white' : 'text-gray-800'}`}>
          {productName}
        </h3>
        
        {/* Brand */}
        {brand && (
          <p className={`text-sm mb-2 ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
            {brand}
          </p>
        )}
        
        {/* Barcode */}
        <p className={`text-xs ${darkMode ? 'text-gray-500' : 'text-gray-500'}`}>
          {barcode}
        </p>
      </div>
    </Link>
  );
};

export default ProductCard; 