import Quagga from 'quagga';
import Tesseract from 'tesseract.js';

// Initialize barcode scanner
export const initBarcodeScanner = (elementId, onDetected) => {
  Quagga.init({
    inputStream: {
      name: "Live",
      type: "LiveStream",
      target: document.querySelector(`#${elementId}`),
      constraints: {
        width: { min: 640 },
        height: { min: 480 },
        facingMode: "environment",
        aspectRatio: { min: 1, max: 2 }
      },
    },
    locator: {
      patchSize: "medium",
      halfSample: true
    },
    numOfWorkers: navigator.hardwareConcurrency || 4,
    decoder: {
      readers: [
        "ean_reader",
        "ean_8_reader",
        "upc_reader",
        "upc_e_reader",
        "code_128_reader",
        "code_39_reader",
        "code_39_vin_reader",
        "codabar_reader",
        "i2of5_reader"
      ]
    },
    locate: true,
    frequency: 10, // Increase frequency for faster detection
    debug: {
      drawBoundingBox: true,
      showFrequency: true,
      drawScanline: true,
      showPattern: true
    }
  }, (err) => {
    if (err) {
      console.error("Error initializing Quagga:", err);
      return;
    }
    
    Quagga.start();
  });

  // Track detection results for confidence
  let detectionResults = [];
  const DETECTION_THRESHOLD = 5; // Number of consistent detections needed
  const CONFIDENCE_THRESHOLD = 0.7; // Minimum confidence level

  // When a barcode is detected
  Quagga.onDetected((result) => {
    if (result && result.codeResult) {
      const code = result.codeResult.code;
      const confidence = result.codeResult.confidence;
      
      // Only consider results with good confidence
      if (confidence > CONFIDENCE_THRESHOLD) {
        // Add to detection results
        detectionResults.push(code);
        
        // Check if we have enough consistent detections
        if (detectionResults.length >= DETECTION_THRESHOLD) {
          // Count occurrences of each code
          const counts = {};
          let maxCount = 0;
          let mostFrequentCode = null;
          
          for (const code of detectionResults) {
            counts[code] = (counts[code] || 0) + 1;
            if (counts[code] > maxCount) {
              maxCount = counts[code];
              mostFrequentCode = code;
            }
          }
          
          // If the most frequent code appears at least 60% of the time
          if (maxCount / DETECTION_THRESHOLD >= 0.6) {
            // Stop scanning
            Quagga.stop();
            
            // Call the callback with the barcode
            onDetected(mostFrequentCode);
          } else {
            // Keep only the last few results
            detectionResults = detectionResults.slice(-DETECTION_THRESHOLD);
          }
        }
      }
    }
  });

  return () => {
    Quagga.stop();
  };
};

// Perform OCR on an image to extract text
export const performOCR = async (imageUrl) => {
  try {
    // First try to detect barcodes in the image
    const barcodeResult = await detectBarcodeInImage(imageUrl);
    
    if (barcodeResult) {
      return `BARCODE: ${barcodeResult}\n\nDetected barcode in image.`;
    }
    
    // If no barcode, perform OCR
    const result = await Tesseract.recognize(
      imageUrl,
      'eng',
      { 
        logger: m => console.log(m),
        tessedit_char_whitelist: 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789.,\'"!@#$%^&*()_+-=[]{}|:;<>?/ '
      }
    );
    
    return result.data.text;
  } catch (error) {
    console.error('Error performing OCR:', error);
    throw error;
  }
};

// Detect barcode in static image
export const detectBarcodeInImage = (imageUrl) => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0, img.width, img.height);
      
      // We don't need to get the image data here since Quagga handles it
      
      Quagga.decodeSingle({
        src: imageUrl,
        numOfWorkers: 0,  // Use main thread
        inputStream: {
          size: Math.max(img.width, img.height)
        },
        decoder: {
          readers: [
            "ean_reader",
            "ean_8_reader",
            "upc_reader",
            "upc_e_reader",
            "code_128_reader",
            "code_39_reader"
          ]
        },
        locate: true,
      }, (result) => {
        if (result && result.codeResult) {
          resolve(result.codeResult.code);
        } else {
          resolve(null);
        }
      });
    };
    
    img.onerror = (error) => {
      reject(error);
    };
    
    img.src = imageUrl;
  });
};

// Extract potential product names from OCR text
export const extractProductInfo = (text) => {
  if (!text) return null;
  
  // Check if a barcode was detected
  const barcodeMatch = text.match(/BARCODE: (\d+)/);
  if (barcodeMatch && barcodeMatch[1]) {
    return {
      barcode: barcodeMatch[1],
      potentialName: 'Product with barcode ' + barcodeMatch[1]
    };
  }
  
  // Split text into lines
  const lines = text.split('\n').filter(line => line.trim().length > 0);
  
  // Simple heuristic: the first few non-empty lines might be the product name
  const potentialName = lines.slice(0, 3).join(' ');
  
  // Look for "Made in" text to identify country of origin
  const madeInMatch = text.match(/made in\s+([a-zA-Z\s]+)/i);
  const countryOfOrigin = madeInMatch ? madeInMatch[1].trim() : null;
  
  // Look for brand name
  const brandMatch = text.match(/by\s+([a-zA-Z\s]+)|brand[:\s]+([a-zA-Z\s]+)/i);
  const brand = brandMatch ? (brandMatch[1] || brandMatch[2]).trim() : null;
  
  return {
    potentialName,
    countryOfOrigin,
    brand,
    barcode: null
  };
};

const scanUtils = {
  initBarcodeScanner,
  performOCR,
  extractProductInfo,
  detectBarcodeInImage
};

export default scanUtils; 