import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAppContext } from '../context/AppContext';
import { getProductByBarcode, getCanadianAlternatives, updateCanadianStatus } from '../services/api';
import ProductCard from '../components/ProductCard';
import toast from 'react-hot-toast';

const ProductDetailsPage = () => {
  const { barcode } = useParams();
  const navigate = useNavigate();
  const { setCurrentProduct, setLoading, setError, darkMode, addToRecentScans, setAlternatives } = useAppContext();
  const [product, setProduct] = useState(null);
  const [isCanadian, setIsCanadian] = useState(false);
  const [alternatives, setLocalAlternatives] = useState([]);
  const [loadingAlternatives, setLoadingAlternatives] = useState(false);
  const [showContributeForm, setShowContributeForm] = useState(false);
  const [userContribution, setUserContribution] = useState({
    isCanadian: null,
    reason: ''
  });

  // Load product details on component mount
  useEffect(() => {
    const fetchProductDetails = async () => {
      if (!barcode) return;
      
      setLoading(true);
      
      try {
        const result = await getProductByBarcode(barcode);
        
        if (result && result.product) {
          setProduct(result.product);
          setCurrentProduct(result.product);
          addToRecentScans(result.product);
          setIsCanadian(result.isCanadian);
          
          // Fetch alternatives if the product is not Canadian
          if (!result.isCanadian) {
            setLoadingAlternatives(true);
            
            try {
              const alternativesResult = await getCanadianAlternatives(barcode);
              
              if (alternativesResult && alternativesResult.products) {
                setLocalAlternatives(alternativesResult.products);
                setAlternatives(alternativesResult.products);
              }
            } catch (err) {
              console.error('Error fetching alternatives:', err);
              toast.error('Failed to get Canadian alternatives');
            } finally {
              setLoadingAlternatives(false);
            }
          }
        } else {
          setError('Product not found');
          toast.error('Product not found');
          navigate('/scan');
        }
      } catch (err) {
        console.error('Error fetching product:', err);
        setError('Failed to fetch product information');
        toast.error('Failed to get product information');
      } finally {
        setLoading(false);
      }
    };

    fetchProductDetails();
  }, [barcode, navigate, setLoading, setError, setCurrentProduct, addToRecentScans, setAlternatives]);

  const handleContributeToggle = () => {
    setShowContributeForm(!showContributeForm);
    
    // Initialize with current value if known
    if (!showContributeForm && isCanadian !== null) {
      setUserContribution({
        ...userContribution,
        isCanadian: isCanadian
      });
    }
  };

  const handleContributeSubmit = async (e) => {
    e.preventDefault();
    
    if (userContribution.isCanadian === null) {
      toast.error('Please select whether this product is Canadian or not');
      return;
    }
    
    setLoading(true);
    
    try {
      const result = await updateCanadianStatus(
        barcode, 
        userContribution.isCanadian, 
        userContribution.reason
      );
      
      if (result && result.product) {
        setProduct(result.product);
        setCurrentProduct(result.product);
        setIsCanadian(result.isCanadian);
        
        toast.success('Thank you for your contribution!');
        setShowContributeForm(false);
        
        // Fetch alternatives if the product is now marked as not Canadian
        if (!result.isCanadian && alternatives.length === 0) {
          setLoadingAlternatives(true);
          
          try {
            const alternativesResult = await getCanadianAlternatives(barcode);
            
            if (alternativesResult && alternativesResult.products) {
              setLocalAlternatives(alternativesResult.products);
              setAlternatives(alternativesResult.products);
            }
          } catch (err) {
            console.error('Error fetching alternatives:', err);
            toast.error('Failed to get Canadian alternatives');
          } finally {
            setLoadingAlternatives(false);
          }
        }
      }
    } catch (err) {
      console.error('Error submitting contribution:', err);
      toast.error('Failed to submit your contribution');
    } finally {
      setLoading(false);
    }
  };

  if (!product) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-4 border-maple-red border-t-transparent"></div>
      </div>
    );
  }

  // Extract product details
  const productName = product.name || product.product_name || 'Unknown Product';
  const brand = product.brand || product.brands || '';
  const imageUrl = product.image_url || '';
  const category = product.category || product.categories || '';
  const description = product.description || product.generic_name || '';
  const ingredients = product.ingredients || product.ingredients_text || '';
  const origin = product.countries || product.origins || '';
  const manufacturer = product.manufacturing_places || '';

  return (
    <div className="pb-16">
      <div className={`rounded-lg shadow-md overflow-hidden ${darkMode ? 'bg-dark-surface' : 'bg-white'}`}>
        {/* Product header */}
        <div className="relative">
          {imageUrl ? (
            <img 
              src={imageUrl} 
              alt={productName} 
              className="w-full h-64 object-contain bg-white"
            />
          ) : (
            <div className="w-full h-64 bg-gray-200 flex justify-center items-center">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-24 w-24 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
              </svg>
            </div>
          )}
          
          {/* Canadian badge */}
          <div className="absolute top-4 right-4">
            {isCanadian ? (
              <div className="bg-maple-red text-white px-3 py-1 rounded-full text-sm font-medium flex items-center">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-1" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                Made in Canada
              </div>
            ) : (
              <div className="bg-gray-700 text-white px-3 py-1 rounded-full text-sm font-medium flex items-center">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-1" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
                Not Canadian
              </div>
            )}
          </div>
        </div>
        
        {/* Product details */}
        <div className="p-6">
          <h1 className="text-2xl font-bold mb-2">{productName}</h1>
          
          {brand && (
            <p className={`text-lg ${darkMode ? 'text-gray-300' : 'text-gray-700'} mb-4`}>
              {brand}
            </p>
          )}
          
          {/* Barcode */}
          <div className="mb-6">
            <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-500'}`}>
              Barcode: {barcode}
            </p>
          </div>
          
          {/* Canadian status details */}
          <div className={`mb-6 p-4 rounded-lg ${isCanadian ? 'bg-green-50 border border-green-200' : 'bg-gray-50 border border-gray-200'} ${darkMode ? 'bg-opacity-10' : ''}`}>
            <h2 className="font-semibold mb-2">
              {isCanadian ? 'Canadian Product Details' : 'Not a Canadian Product'}
            </h2>
            
            {origin && (
              <p className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'} mb-1`}>
                <span className="font-medium">Origin:</span> {origin}
              </p>
            )}
            
            {manufacturer && (
              <p className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'} mb-1`}>
                <span className="font-medium">Manufacturer:</span> {manufacturer}
              </p>
            )}
            
            {/* Canadian factors if available */}
            {product.canadian_factors && (
              <div className="mt-2">
                <p className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'} mb-1`}>
                  <span className="font-medium">Canadian Factors:</span>
                </p>
                <ul className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'} list-disc pl-5`}>
                  {product.canadian_factors.countries && (
                    <li>Made in Canada</li>
                  )}
                  {product.canadian_factors.manufacturing && (
                    <li>Manufactured in Canada</li>
                  )}
                  {product.canadian_factors.origins && (
                    <li>Canadian origin</li>
                  )}
                  {product.canadian_factors.canadian_brand && (
                    <li>Canadian brand</li>
                  )}
                  {product.canadian_factors.user_verified && (
                    <li>Verified by users</li>
                  )}
                </ul>
              </div>
            )}
            
            {/* Contribute button */}
            <button
              onClick={handleContributeToggle}
              className="mt-3 text-sm text-maple-red hover:text-red-700 font-medium"
            >
              {showContributeForm ? 'Cancel' : 'Contribute Information'}
            </button>
            
            {/* Contribute form */}
            {showContributeForm && (
              <form onSubmit={handleContributeSubmit} className="mt-3">
                <div className="mb-3">
                  <p className={`text-sm font-medium mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                    Is this product made in Canada?
                  </p>
                  <div className="flex space-x-4">
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        name="isCanadian"
                        value="yes"
                        checked={userContribution.isCanadian === true}
                        onChange={() => setUserContribution({...userContribution, isCanadian: true})}
                        className="form-radio h-4 w-4 text-maple-red"
                      />
                      <span className={`ml-2 text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>Yes</span>
                    </label>
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        name="isCanadian"
                        value="no"
                        checked={userContribution.isCanadian === false}
                        onChange={() => setUserContribution({...userContribution, isCanadian: false})}
                        className="form-radio h-4 w-4 text-maple-red"
                      />
                      <span className={`ml-2 text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>No</span>
                    </label>
                  </div>
                </div>
                
                <div className="mb-3">
                  <label className={`block text-sm font-medium mb-1 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                    Reason (optional)
                  </label>
                  <textarea
                    value={userContribution.reason}
                    onChange={(e) => setUserContribution({...userContribution, reason: e.target.value})}
                    className={`w-full px-3 py-2 text-sm rounded-md ${darkMode ? 'bg-dark-input text-gray-300 border-gray-700' : 'bg-white text-gray-700 border-gray-300'} border`}
                    rows="2"
                    placeholder="e.g., I checked the packaging and it says 'Made in Canada'"
                  ></textarea>
                </div>
                
                <button
                  type="submit"
                  className="bg-maple-red text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-red-700 transition duration-200"
                >
                  Submit
                </button>
              </form>
            )}
          </div>
          
          {/* Product details */}
          {(category || description || ingredients) && (
            <div className="mb-6">
              <h2 className="text-lg font-semibold mb-2">Product Details</h2>
              
              {category && (
                <p className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'} mb-2`}>
                  <span className="font-medium">Category:</span> {category}
                </p>
              )}
              
              {description && (
                <div className="mb-2">
                  <p className={`text-sm font-medium ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>Description:</p>
                  <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>{description}</p>
                </div>
              )}
              
              {ingredients && (
                <div>
                  <p className={`text-sm font-medium ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>Ingredients:</p>
                  <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>{ingredients}</p>
                </div>
              )}
            </div>
          )}
          
          {/* Data source */}
          {product.data_sources && (
            <div className="mt-6 pt-4 border-t border-gray-200">
              <p className={`text-xs ${darkMode ? 'text-gray-400' : 'text-gray-500'}`}>
                Data source: {product.data_sources.source}
                {product.data_sources.last_updated && (
                  <span> (Updated: {new Date(product.data_sources.last_updated).toLocaleDateString()})</span>
                )}
              </p>
            </div>
          )}
        </div>
      </div>
      
      {/* Canadian alternatives */}
      {!isCanadian && (
        <div className="mt-8">
          <h2 className="text-xl font-bold mb-4">Canadian Alternatives</h2>
          
          {loadingAlternatives ? (
            <div className="flex justify-center items-center h-32">
              <div className="animate-spin rounded-full h-8 w-8 border-4 border-maple-red border-t-transparent"></div>
            </div>
          ) : alternatives.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {alternatives.map((alt) => (
                <ProductCard 
                  key={alt.barcode} 
                  product={alt} 
                  isCanadian={true}
                />
              ))}
            </div>
          ) : (
            <div className={`p-4 rounded-lg ${darkMode ? 'bg-dark-surface' : 'bg-gray-50'} text-center`}>
              <p className={darkMode ? 'text-gray-300' : 'text-gray-700'}>
                No Canadian alternatives found for this product.
              </p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default ProductDetailsPage;