import React, { createContext, useContext, useState, useEffect } from 'react';
import { getDarkMode, toggleDarkMode as toggleDarkModeUtil } from '../utils/darkMode';

const AppContext = createContext();

export const useAppContext = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
  const [searchResults, setSearchResults] = useState([]);
  const [recentScans, setRecentScans] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [currentProduct, setCurrentProduct] = useState(null);
  const [alternatives, setAlternatives] = useState([]);
  const [darkMode, setDarkMode] = useState(false);

  // Initialize dark mode from localStorage
  useEffect(() => {
    const savedDarkMode = getDarkMode();
    setDarkMode(savedDarkMode);
    
    // Apply dark mode class to body
    if (savedDarkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, []);

  // Update body class when dark mode changes
  useEffect(() => {
    if (darkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [darkMode]);

  // Toggle dark mode
  const toggleDarkMode = () => {
    const newMode = toggleDarkModeUtil();
    setDarkMode(newMode);
  };

  // Load recent scans from localStorage on initial load
  useEffect(() => {
    const savedScans = localStorage.getItem('recentScans');
    if (savedScans) {
      setRecentScans(JSON.parse(savedScans));
    }
  }, []);

  // Save recent scans to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('recentScans', JSON.stringify(recentScans));
  }, [recentScans]);

  // Add a product to recent scans
  const addToRecentScans = (product) => {
    setRecentScans(prevScans => {
      // Remove the product if it already exists
      const filteredScans = prevScans.filter(scan => scan.code !== product.code);
      
      // Add the product to the beginning of the array
      return [product, ...filteredScans].slice(0, 10);
    });
  };

  // Clear search results
  const clearSearchResults = () => {
    setSearchResults([]);
  };

  // Clear error
  const clearError = () => {
    setError(null);
  };

  return (
    <AppContext.Provider
      value={{
        searchResults,
        setSearchResults,
        recentScans,
        loading,
        setLoading,
        error,
        setError,
        currentProduct,
        setCurrentProduct,
        alternatives,
        setAlternatives,
        addToRecentScans,
        clearSearchResults,
        clearError,
        darkMode,
        toggleDarkMode: toggleDarkMode
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppContext; 